@import url(https://fonts.googleapis.com/css2?family=Oswald&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


body{padding:10px;background-color:#2f2f2f;display:flex;align-items:center;justify-content:center}.JokeTemplate{border-radius:10px;overflow:hidden;width:700px;font-family:"Oswald", sans-serif}.JokeHeader_title{text-align:center;font-size:40px;padding:10px;font-weight:bolder;background:#833ab4;background:linear-gradient(90deg, #833ab4 0%, #fd1d1d 50%, #fcb045 100%)}.JokeBody{padding:5px;background-color:#f2f2f2;height:800px;overflow-y:auto}.JokeBody_setup{background:#eeaeca;background:radial-gradient(circle, #eeaeca 0%, rgba(148,187,233,0.48223) 100%);text-align:center;padding:10px}.JokeBody_punch{display:flex;justify-content:center;align-items:center;flex-direction:column}.JokeBody_punch_line{font-size:100px}.JokeBody_reload{display:flex;justify-content:center;align-items:flex-end;cursor:pointer;background:#eeaeca;background:radial-gradient(circle, #eeaeca 0%, rgba(148,187,233,0.48223) 100%);border-radius:15px;margin-top:110px}.JokeFooter{background:#833ab4;background:linear-gradient(90deg, #833ab4 0%, #fd1d1d 50%, #fcb045 100%)}.JokeFooter_maker{text-align:center;padding:10px}

